/* arrow for step process */
.arrow {
  float: left;
  width: 22%;
  height: 50px;
  background-color: rgba(55, 118, 29, 0.3);
  border-radius: 5px 5px 0 0;
  position: relative;
  margin-right: 18px;
  margin-bottom: 20px;
  text-align: center;
  line-height: 45px; }
  .arrow > a {
    display: block;
    text-decoration: none;
    color: #37761D;
    font-size: 14px;
    font-weight: bold;
    padding: 0 12px; }
    .arrow > a > i {
      padding-left: 15px; }
  @media (max-width: 990px) {
    .arrow {
      width: 21%; } }
  @media (max-width: 512px) {
    .arrow {
      width: 18%; } }
  @media (max-width: 414px) {
    .arrow > a {
      padding: 0; } }

.arrow:after {
  content: '';
  position: absolute;
  right: -22px;
  top: 30%;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-left: 12px solid rgba(55, 118, 29, 0.3); }

.arrow-active {
  background-color: rgba(55, 118, 29, 0.7); }
  .arrow-active > a {
    color: #fff; }

.arrow-active:after {
  border-left: 12px solid rgba(55, 118, 29, 0.7); }

.arrow-none:after {
  border: 0; }

.cursor-not-allow {
  cursor: not-allowed; }

.cursor-initial {
  cursor: default; }

.cursor-pointer {
  cursor: pointer; }

